@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&family=Roboto:wght@100;300;500;700;900&display=swap);
body {
  margin: 0;

  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  color: #828282;
  font-size: small;

  line-height: 1.5;

  padding-top: 40px;
  padding-bottom: 8px;
  margin-top: 40px;

  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  text-decoration: none;
  color: #333333;
}

a:hover {
  color: #4e4bec;
}


.Art_wrapper__1_Awy {
  margin-top: 5vh;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Art_artboard__3ctTu {
  display: flex;
  justify-content: center;
  align-items: center;
}

canvas {
  border: 2px solid white;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.Art_canvasMenu__WG4So {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: top;
  background-color: rgba(1, 22, 39, 1);
  color: white;
  padding: 12px;
  max-width: 200px;
  border-radius: 0 4px 4px 0;
}

hr {
  color: white;
  width: 200px;
}

.Art_cvBtns__3Woxl {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: right;
}

.Art_canvasMenu__WG4So button {
  background-color: rgba(255, 255, 255, 1);
  padding: 12px;
  border: none;
  border-radius: 2px;
  min-width: 200px;
  font-weight: bold;
}

.Art_canvasMenu__WG4So button:hover {
  font-weight: bold;
}

.Art_canvasMenu__WG4So h1 {
  font-size: large;
  font-weight: 700;
}
.Art_canvasMenu__WG4So h2 {
  font-size: medium;
  font-weight: 500;
}

.Art_canvasMenu__WG4So p {
  font-size: medium;
  font-weight: 300;
}

.Art_dimensions__mh8dx {
  font-size: smaller;
  color: rgba(255, 255, 255, 0.6);
}

select {
  color: rgba(1, 22, 39, 1);
  height: 48px;
  min-width: 200px;
  padding-left: 4px;
  padding-right: 0;
  font-size: small;
  font-weight: medium;
  margin-top: 4px;
  margin-bottom: 12px;
  border: none;
  border-radius: 2px;
}

label {
  font-size: small;
  font-weight: medium;
}

nav {
  margin: 0;
  background-color: #011627;
  height: 48px;
  width: 100%;
  box-shadow: 0px 4px 4px rgba(50, 50, 50, 0.25);
}

.Navbar_navbar__AYGyQ {
  margin-left: 10vw;
  margin-right: 10vw;
  max-width: 960px;

  padding-left: 32px;

  display: flex;
  flex-direction: row;
  align-items: center;
}

img {
  height: 20px;
}

.Navbar_logo__2l4jJ {
  color: white;
  padding-left: 8px;
  font-family: "Roboto Mono";
  font-size: 18px;
  margin-top: 12px;
}

